@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  background-color: #fff;
  color: #333;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.main {
  max-width: 1100px;
  margin: auto;
  overflow: hidden;
  padding: 0 2rem;
  color: #056674;
}
.navbar {
  background-color: #056674;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.7rem 2rem;
  z-index: 1;
  width: 100%;
  opacity: 0.9;
  margin-bottom: 1rem;
}

.navbar ul {
  display: flex;
}

.navbar a {
  color: #fff;
  padding: 0.45rem;
  margin: 0 0.25rem;
}

.navbar .welcome span {
  margin-right: 0.6rem;
}
.navbar {
  display: block;
  text-align: left;
  cursor: pointer;
  padding-left: 7rem;
}

.navbar ul {
  text-align: center;
  justify-content: center;
}

.grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
}
/* Forms */

.form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888;
}
input[type="date"],
input[type="email"],
input[type="password"],
input[type="text"],
input[type="number"],
select,
textarea {
  display: block;
  width: 100%;
  padding: 0.4rem;
  font-size: 1.2rem;
  border: 1px solid #ccc;
  transition: ease-in-out, width 0.35s ease-in-out;
}

input {
  margin: 1.2rem 0;
}

textarea:focus,
input[type="text"]:focus,
input[type="password"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="time"]:focus,
input[type="week"]:focus,
input[type="number"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="search"]:focus,
input[type="tel"]:focus,
input[type="color"]:focus,
.uneditable-input:focus {
  border-color: rgb(5, 102, 116, 0.8);
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset, 0 0 8px rgba(126, 239, 104, 0.6);
  outline: 0 none;
}
.form-group {
  margin: 1.2rem 0;
}

.form-group input {
  margin: 0.2rem 0;
}

.form-container {
  max-width: 500px;
  margin: 2rem auto;
  overflow: hidden;
  padding: 0 2rem;
}

.form-container h1,
h2,
h3 {
  text-align: center;
}
input[type="submit"],
button {
  font: inherit;
}
.btn {
  display: inline-block;
  background: #056674;
  color: rgb(255, 255, 255);
  padding: 0.4rem 1.3rem;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  transition: opacity 0.2s ease-in;
  outline: none;
}

.btn-link {
  background: none;
  padding: 0;
  margin: 0;
}
.btn-delete {
  color: rgb(255, 255, 255);
  float: right;
  padding: 0.4rem 1.3rem;
  background-color: #ff4b5c;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  outline: none;
}

.btn-block {
  display: block;
  width: 100%;
}

.btn-sm {
  font-size: 0.8rem;
  padding: 0.3rem 1rem;
  margin-right: 0.2rem;
}

li {
  color: grey;
  padding-top: 3px;
  list-style: none;
  text-align: -webkit-match-parent;
  font-size: 15px;
}
.note {
  color: white;
  padding-top: 5px;
  font-size: 15px;
}

.error {
  font: inherit;
  text-align: -webkit-match-parent;
  padding: 0.4em;
  font-weight: bold;
  color: white;
  background: #ff4b5c;
}
.success {
  font: inherit;
  text-align: -webkit-match-parent;
  padding: 0.4em;
  font-weight: bold;
  color: white;
  background: #66bfbf;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  padding: 1rem;
  border: #e0ece4 2px;
  margin: 0.7rem 0;
  display: grid;
  grid-template-columns: 2fr 1fr;
  align-items: center;
  line-height: 1.8;
  margin-bottom: 1rem;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

